import * as React from 'react';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Layout from '../components/common/Layout/Layout';
import {
  GatsbySeoComponent,
  MetaData,
  TopLevelPageJSONLDSchemas,
} from '../components/common/SEO';
import { StaticPageFrontmatter } from '../types/StaticPageFrontmatter';
import {
  CtaBlock,
  FeatureComparisonSection,
  HeroSection,
  QuotesSection,
} from '../components/telgorithm-vs-twilio';
import { Container } from '../components/common/Container/Container';
import Partners from '../components/telgorithm-vs-twilio/Partners';
import { Text } from '../components/common';
import { StaticImage } from 'gatsby-plugin-image';
import {
  CalculatorContainer,
  CtaBlockBody,
} from '../components/telgorithm-vs-twilio/styled';
import { BulletList, SectionContainer, StyledOl } from '../components/common/styled';
